.page-footer {
  background: linear-gradient(180deg, $default-color 0 57%, $second-color 57% 100%);
}

.page-footer--background {
  background: $second-color;
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  @media (min-width: $tabletWidth) {
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 60px;
  }

  @media (min-width: $desktopWidth) {
    padding-bottom: 35px;
  }
}

.page-footer__logo {
  display: block;
  padding: 35px 0 33px;

  border-bottom: 1px solid $line-color;

  @media (min-width: $tabletWidth) {
    padding: 60px 0 0;
    margin: 0;
    border: none;
  }

  @media (min-width: $desktopWidth) {
    padding: 55px 0 0;
  }
}

.page-footer__socials {
  padding: 21px 0 16px;

  border-bottom: 1px solid $line-color;

  @media (min-width: $tabletWidth) {
    padding: 60px 0 0;
    border: none;
  }

  @media (min-width: $desktopWidth) {
    padding: 55px 0 0;
  }
}
